import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';

import { ConfirmationService, MenuItem, MessageService, SelectItem } from 'primeng/api';
import { Dropdown } from 'primeng/dropdown';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';

import { StorageService } from './../../../../services/auth/storage.service';
import { PageableDto } from './../../../../interfaces/others/pageable.dto';
import { PageOrder } from './../../../../constants/page.constant';
import { ToastEnum, ToastPrimeSeverityEnum } from '../../../../../shared/constants/toast.constant';
import { UserDto } from '../../../../../shared/interfaces/users/user.dto';
import { ToastMessageService } from './../../../../services/toast/toast-message.service';
import { UserService } from '../../../../../shared/services/user/user.service';
import { CondominioService } from '../../../../services/condominio/condominio.service';
import { StatusServico, StatusServicoEnum,  } from './../../../../constants/status.constant';
import { CreatePrestacaoServicoDto, PrestacaoServicoDto } from './../../../../../shared/interfaces/servicos/prestacao-servico.dto';
import { TipoUsuarioEnum } from './../../../../constants/tipo-user.constant';
import { CondominioDto } from '../../../../interfaces/condominios/condominio.dto';
import { PrestacaoServicosService } from './../../../../services/prestacao-servicos/prestacao-servicos.service';
import { LocalAgendamento } from 'src/app/shared/constants/agendamento.constant';
import { ServicosUpdateComponent } from '../../simplificado/servicos-update/servicos-update.component';
import { EnderecoDto } from './../../../../interfaces/enderecos/endereco.dto';

@Component({
  selector: 'app-prestacao-servicos-gestao-main',
  templateUrl: './prestacao-servicos-gestao-main.component.html',
  styleUrls: ['./prestacao-servicos-gestao-main.component.scss'],
})
export class PrestacaoServicosGestaoMainComponent implements OnInit, OnDestroy {

  prestacoesServico: PageableDto<PrestacaoServicoDto>;
  searchedPrestacaoServico = '';
  selectedPrestacaoServico: PrestacaoServicoDto;
  selectedStatusServico;
  currentPage = 1;

  colaboradores: PageableDto<UserDto>;
  searchedColaborador = '';
  selectedColaborador = '';

  condominios: PageableDto<CondominioDto>;
  searchedcondominio = '';
  selectedcondominio = '';
  shouldDisableColaborador = false;

  sortOptions: SelectItem[] = [];
  isLoaded = false;
  currentcondominioLoaded: string;


  selectedEndereco: EnderecoDto;
  showEnderecoDialog = false;
  showServicoDialog = false;

  private subscriptions = new Subscription();

  constructor(
    private readonly userService: UserService,
    private readonly modal: ModalController,
    private readonly toastService: ToastMessageService,
    private readonly condominioService: CondominioService,
    private readonly router: Router,
    private readonly prestacaoServicosService: PrestacaoServicosService,
    private readonly storageService: StorageService,
    private readonly confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    const { id: user_id, condominio } = this.storageService.getCompleteLocalUser();
    this.selectedColaborador = user_id;
    this.selectedcondominio = condominio.id;
    this.sortOptions = [...StatusServico];

    this.loadcondominios();
    this.loadFuncionarios();
    this.loadPrestacaoServico();

  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  changeFiltro({ value }) {
    this.selectedStatusServico = value;
    this.loadPrestacaoServico(1);
  }

  clearFilter() {
    this.loadPrestacaoServico(1);
  }

  clearAllFilters(event, dropdown_condominios: Dropdown, dropdown_filtro: Dropdown) {
    this.searchedPrestacaoServico = undefined;
    this.selectedcondominio = undefined;
    this.clearcondominioFilter(null);

    dropdown_condominios.writeValue(null);
    dropdown_filtro.writeValue(null);


    this.loadFuncionarios(1);
  }

  createNewPrestacao() {
    this.router.navigateByUrl('prestacao-servico-simples');
  }

  setUserImage(user: UserDto) {
    return user.avatar_url || "assets/imgs/outros/default-user.jpg";
  }


  //#region condominio
  changecondominio({ value }) {
    if (this.selectedcondominio) {
      this.shouldDisableColaborador = false;
      this.loadFuncionarios(1);
      this.loadPrestacaoServico(1);
    }
  }

  clearcondominioFilter(event) {
    this.shouldDisableColaborador = true;
    this.selectedColaborador = undefined;
    this.selectedStatusServico = undefined;
  }

  loadcondominios(page = 1, dropdown?: Dropdown) {
    const sub = this.condominioService.list(
      { searchedCondominio: this.searchedcondominio },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.condominios = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }
  paginarcondominio(event) {
    this.loadcondominios();
  }
  oncondominioFilter(event) {
    const filter = event.filter as string;
    this.searchedcondominio = filter;
  }

  blurcondominio(event, dropdown: Dropdown) {
    this.searchedcondominio = undefined;
    dropdown.filterValue = undefined;
  }
  //#endregion

  //#region COLABORADOR
  changeColaborador({ value }) {
    this.loadFuncionarios(1);
  }

  loadFuncionarios(page = 1) {
    const sub = this.userService.listUsers(
      {
        condominio_id: this.selectedcondominio,
        searchedUser: this.searchedColaborador,
        tipo_usuario: TipoUsuarioEnum.FUNCIONARIO
      },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.colaboradores = response;
      });
    this.subscriptions.add(sub);
  }
  paginarColaborador(event) {
    this.loadFuncionarios();
  }
  onColaboradorFilter(event) {
    const filter = event.filter as string;
    this.searchedColaborador = filter;
  }
  blurColaborador(event, dropdown: Dropdown) {
    this.searchedColaborador = undefined;
    dropdown.filterValue = undefined;
  }
  //#endregion

  //#region PRESTACAO-SEVICO

  searchPrestacaoServico() {
    this.loadPrestacaoServico(1);
  }

  paginarPrestacaoServico(event) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentPage) {
      this.currentPage = page;
      this.loadPrestacaoServico(page);
    }
  }

  loadPrestacaoServico(page = 1) {
    const sub = this.prestacaoServicosService.list(
      {
        condominio_id: this.selectedcondominio,
        colaborador_id: this.selectedColaborador,
        searchedPrestacaoServico: this.searchedPrestacaoServico,
        status_servico: this.selectedStatusServico,
      },
      { order: PageOrder.DESC, page, take: 10 }
    )
      .subscribe(response => {
        this.prestacoesServico = response;
      });
    this.subscriptions.add(sub);
  }
  //#endregion

  //#region OPERACOES
  openEndereco(prestacaoServico?: PrestacaoServicoDto) {
    if (!prestacaoServico && this.selectedPrestacaoServico) {
      prestacaoServico = this.selectedPrestacaoServico;
    }

    if (!prestacaoServico && !this.selectedPrestacaoServico) {
      this.toastService.presentToast({
        detalhe: `Nenhuma prestação selecionada`,
        titulo: `Atenção`,
        gravidade: ToastPrimeSeverityEnum.ATENCAO,
        duracao: ToastEnum.shortDuration,
      });
      return;
    }

    const { local } = prestacaoServico;

    if (local == LocalAgendamento.LOCAL || local == LocalAgendamento.EXTERNO) {
      const endereco = prestacaoServico.agendamento_servico?.endereco || prestacaoServico.condominio.endereco;
      this.selectedEndereco = endereco;
      this.showEnderecoDialog = true;
      return;
    }

    this.toastService.presentToast({
      detalhe: `Atendimento remoto`,
      titulo: `Remoto`,
      gravidade: ToastPrimeSeverityEnum.INFORMACAO,
      duracao: ToastEnum.shortDuration,
    });
  }

  openDetailPrestacaoServico(prestacaoServico?: PrestacaoServicoDto) {
    if (this.selectedPrestacaoServico) {
      this.showServicoDialog = true;
      return;
    }

    this.selectedPrestacaoServico = prestacaoServico;
    this.showServicoDialog = true;
  }

  startPrestacaoServico(prestacaoServico: PrestacaoServicoDto) {
    const { id, cliente } = prestacaoServico;
    const sub = this.prestacaoServicosService.initiateById(id)
      .subscribe(response => {
        this.toastService.presentToast({
          titulo: `Iniciado`,
          detalhe: `Serviço do cliente '${cliente.nome}' iniciado`,
          duracao: ToastEnum.mediumDuration,
          gravidade: ToastPrimeSeverityEnum.INFORMACAO,
        });
        this.loadPrestacaoServico(1);
      });
    this.subscriptions.add(sub);
  }

  finalizePrestacaoServico(prestacaoServico: PrestacaoServicoDto) {
    const { id, cliente } = prestacaoServico;
    const sub = this.prestacaoServicosService.finalizeById(id)
      .subscribe(response => {
        this.toastService.presentToast({
          titulo: `Finalizado`,
          detalhe: `Serviço do cliente '${cliente.nome}' finalizado`,
          duracao: ToastEnum.mediumDuration,
          gravidade: ToastPrimeSeverityEnum.SUCESSO,
        });
        this.loadPrestacaoServico(1);
      });
    this.subscriptions.add(sub);
  }

  cancelPrestacaoServico(prestacaoServico: PrestacaoServicoDto) {
    const { id, cliente } = prestacaoServico;

    this.confirmationService.confirm({
      message: `Deseja mesmo cancelar a prestação de serviço ${id}?`,
      header: `Prestação do cliente '${cliente.nome}'`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const sub = this.prestacaoServicosService.cancelById(id)
          .subscribe(response => {
            this.toastService.presentToast({
              titulo: `Cancelado`,
              detalhe: `Serviço do cliente '${cliente.nome}' cancelado`,
              duracao: ToastEnum.mediumDuration,
              gravidade: ToastPrimeSeverityEnum.INFORMACAO,
            });
            this.loadPrestacaoServico(1);
          });
        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastService.clearToast();
      }
    });


  }

  updatePrestacaoServico(prestacaoServico: PrestacaoServicoDto) {
    this.showModal(ServicosUpdateComponent, { prestacaoServico });
  }

  showStartButton(prestacaoServico: PrestacaoServicoDto) {
    const { status_servico } = prestacaoServico;

    if (status_servico == StatusServicoEnum.AGENDADO) {
      return true;
    }

    return false;
  }

  showFinalizeButton(prestacaoServico: PrestacaoServicoDto) {
    const { status_servico } = prestacaoServico;

    if (status_servico == StatusServicoEnum.INICIADO) {
      return true;
    }

    return false;
  }

  showCancelButton(prestacaoServico: PrestacaoServicoDto) {
    const { status_servico } = prestacaoServico;

    if (status_servico == StatusServicoEnum.AGENDADO || status_servico == StatusServicoEnum.INICIADO) {
      return true;
    }

    return false;
  }
  //#endregion

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      this.isLoaded = false;
      if (hasUpdate) {
        this.loadPrestacaoServico();
      }
    });

    return await modal.present();
  }
}
